<template>
  <div>
    <!--<remote-css the-href="https://cdnjs.cloudflare.com/ajax/libs/materialize/0.100.2/css/materialize.min.css"></remote-css>-->
    <form ref="form" :action="action">
      <link rel="stylesheet" type="text/css" :href="baseUrl + '/styles/default/css/form/form.css'">
      <link rel="stylesheet" type="text/css" :href="baseUrl + '/styles/default/css/form/table.css'">
      <link rel="stylesheet" type="text/css" :href="baseUrl + '/styles/default/css/form/style.css'">
      <link rel="stylesheet" type="text/css" :href="baseUrl + '/styles/default/css/form/style.css'">

