      }
    },
    mounted () {
      // const s = document.createElement('script')
      // s.src = 'http://192.168.1.108:8080/sws/styles/default/css/form/js/form.js'
      // s.type = 'text/css'
      // s.href = 'https://cdnjs.cloudflare.com/ajax/libs/materialize/0.100.2/css/materialize.min.css'
      // s.rel = 'stylesheet'
      // console.log(s)
    },
    methods: {
      submit () {
        this.$refs.form.sumbmit()
      }
    },
    components: {
      // remoteCss
    }
  }
</script>

