<template>
</template>

<script>
  export default {
    name: 'remote-css',
    render: function (createElement) {
      return createElement('link', {attrs: { type: 'text/css', rel: 'stylesheet', href: this.theHref }})
    },
    props: {
      theHref: { type: String, required: true }
    }
  }
</script>

<style scoped>

</style>



