<template>
  <div>
    <!--<remote-css theHref="http://192.168.1.108:8080/sws/styles/default/css/form.css"></remote-css>-->
    <el-row>
      <el-col :span="4" :offset="11">
        <span class="top-text">施工信息</span>
      </el-col>
      <el-col :span="2" :offset="7">
        <el-button class="submit-button" @click="visible = true">提交</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="16" :offset="4">
        <div >
<!--
          <form v-html="formHtml">
          </form>-->
          <form-html :form-html-str="formHtml"
                     ref="form"
                     :action="actionUrl"></form-html>
          <!--<h2>helljsdofijsofj</h2>-->
        </div>
      </el-col>
    </el-row>
    <submit-form :visible.sync="visible" :tip-text="项目报建" @on-close="handleClose"></submit-form>
  </div>

</template>

<script>
  // import {getFormHtml} from '../../api/project'
  import submitForm from '../../components/common/submitForm'
  import remoteCss from '../../components/common/remoteCss'
  import formHtml from '../../components/common/formHtml'

  import * as projectApi from '../../api/project/index'

  // import New from "./project";

  export default {
    name: 'new-project',
    data () {
      return {
        formHtml: '',
        visible: false,
        actionUrl: ''
      }
    },
    mounted () {
      this.initFormHtml()
    },
    methods: {
      initFormHtml () {
        this.actionUrl = process.env.baseUrl + '/platform/lingang/enginConstr/startFlow.ht'
        projectApi.getFormHtml()
          .then(res => {
            this.formHtml = res.data
            // alert(res.data)
          })
          .catch(res => {
            console.log(res)
          })
      },
      handleClose () {
        // this.$router.push({paths: '/index'})
      }
    },
    components: {
      remoteCss,
      formHtml,
      submitForm
    }
  }
</script>

<style scoped>
  /*@import url("https://cdnjs.cloudflare.com/ajax/libs/materialize/0.100.2/css/materialize.min.css");*/
  .top-text{
    font-size: 20px;
    color: #4889D8;
    font-weight: bold;
  }
  .submit-button{
    width: 100px;
    background-color: #70CEA9;
    color: white;
  }

</style>



